import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
    HashRouter as Router, 
    Switch, 
    Route, 
    NavLink, 
    Redirect } from 'react-router-dom';

import { 
    Header, 
    HeaderNav, 
    NavItem,
    NavHumburger,
    Line,
    NavWrapper } from '../styles/Header';

import { GlobalStyle } from '../styles/GlobalStyle';

import { LINKS } from './links';

import { Profile } from './Profile/Profile';
import { Skills } from './Skills/Skills';
import { Works } from './Works/Works';
import { Resume } from './Resume/Resume';


export const App = () => {
    const [isOpen, setIsOpen] = useState('');
    const toggleMenu = () => isOpen === 'open' ? setIsOpen('') : setIsOpen('open');

    return (
        <>
        <GlobalStyle /> 
        <Router>
            <Header>
                <HeaderNav>
                    <NavHumburger onClick={toggleMenu} >
                        <Line />
                        <Line />
                        <Line />
                    </NavHumburger>
                    <NavWrapper className={isOpen}>
                        {LINKS.map(({ path, avatar, title }) =>
                            <NavItem key={title}>
                                <NavLink to={path} activeStyle={{ background: '#203748' }} >
                                    <FontAwesomeIcon icon={avatar} size="lg" />
                                    <span>{title}</span>
                                </NavLink>
                            </NavItem>
                        )}
                    </NavWrapper>
                </HeaderNav>
            </Header>
            
            <Switch>
                <Route path='/profile' component={Profile}/>
                <Route path='/skills' component={Skills}/>
                <Route path='/works' component={Works}/>
                <Route path='/resume' component={Resume}/>
                <Redirect to="/profile" />
            </Switch>
        </Router>
        </>
    );
}
